import { Container, Row, Col, Badge } from 'react-bootstrap'
import { NavLink } from "react-router-dom";
import Paragraph from './Paragraph';
import TaskTable from './TaskTable';
import SolutionBlock from './SolutionBlock';

const TaskPage = () => {
    return (
        <Container fluid className="section-right-margin p-md-5 p-2">
            <Row className="mb-3 mx-0">
                <NavLink to="/class/tasks" className="text-decoration-none blue-link mb-3 px-0">
                    <span> </span>
                </NavLink>
                <h2 className="news-page-header mb-2 px-0"> </h2>
                <div className="sub-header-info d-flex align-items-center px-0">
                    <Badge pill className="points-badge mr-3">5 </Badge>
                    <span className="solved-number">: 42</span>
                </div>
            </Row>
            <Row className="mx-0">
                <Col className="px-0 mr-4">
                    <Paragraph className="mb-5" header="" paragraphs={[`  `, `        ,       (   ,    )    ,        .       ,      ,       .  ,           ,        ,        " ".`, `paragarph 3 adsgsdgsad`]} />
                    <Paragraph className="mb-4" header=" " paragraphs={[' -   n (n <= 109).']} />
                    <Paragraph className="mb-4" header=" " paragraphs={['       ,   ,  .       .']} />
                    <TaskTable header="" />
                </Col>
                <Col md="auto" className="px-0 mx-auto">
                    <SolutionBlock />
                </Col>
            </Row>
        </Container>
    )
}

export default TaskPage